var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("關於美仕媞")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("關於我們")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"關於我們","titleEn":"ABOUT MAXBEAUTY"}}),_vm._ssrNode(" <div class=\"article-container\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/about/1-1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/about/1-1.jpg")))+" alt=\"關於美仕媞的醫療背景\"></picture> <h1>醫療背景</h1> <p>美仕媞時尚醫美診所是由在SPA界近30年的詩嫚特美容集團，轉投資合作打造的醫美中心，篩選出專業精英級醫療團隊，透過集團資源共享與支援，引進最新及頂尖的醫療設備，提供專業的個人專屬療程。 團隊堅持著「專業療效、完美修護」的理念，透過專業的醫師、護理人員的經驗與技術，建構一個高品質的醫療空間，嚴選先進安全的醫美儀器，給予全方面量身訂做的專業療程。</p> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/about/3.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/about/3.jpg")))+" alt=\"關於美仕媞的醫療背景\"></picture></div> <div class=\"article-container\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/about/8.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/about/8.jpg")))+" alt=\"坐擁101露天吧台百萬夜景\"></picture> <h1>坐擁101露天吧台百萬夜景</h1> <p>誕生於2014年8月份的美仕媞時尚醫美診所，全台首創斥資千萬打造絕佳高空環景明亮視野，這是專屬妳的私房景點，美仕媞時尚醫美座落於松山機場前，俯瞰燦爛奪目的魅力101及美麗華摩天輪美景，這片彷若灑落一地的夜光珍寶，將繁華台北迷人風貌盡收眼底。</p> <p>美仕媞時尚醫美中心，為詩嫚特美容國際集團轉投資合作之醫美中心，篩選出專業精英級醫療團隊，透過集團間資源共享與支援，引進最新美容技術及頂尖設備，提供安心的個人專屬療程。</p></div> <div class=\"article-container\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/about/4.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/about/4.jpg")))+" alt=\"美麗旅程\"></picture> <h1>美麗旅程</h1> <p>美仕媞位於松山機場前，透過與旅行業者的合作，緊密結合觀光旅遊及醫美行程，為目前陸客來台醫美診所優選。</p> <p>目前兩岸往來密切，在直航後所帶來的觀光醫療龐大商機，主要原因是因為相較於中國發展醫學美容，台灣領先中國至少六年，台灣擁有專業的技術、設備與醫療團隊，且在地緣與語言上占有優勢，更勝於韓國，所以非常吸引大陸人來台觀光與安排醫美療程。</p> <p>要想發展觀光結合醫美，必須掌握幾個關鍵性的概念，包括 : 絕對的安全、絕對有效、修復期短，唯有如此，才能符合陸客來台醫療觀光的需求。以美仕媞的經驗，陸客來台安排觀光與醫美療程，必須以「效果．修復期短」的療程，才能提供陸客最快速且安全的療程服務。</p> <p>最重要是讓台灣醫療形象讓陸客滿意民眾肯定，才是作好服務陸客的關鍵。</p> <p>陸客來台最愛做的有4D埋線拉提、極線音波拉皮。主要是因為來台時間短，需要短時間看到效果的療程，像4D埋線拉提因為效果快速，可以刺激膠原蛋白增生，改善嘴邊肉、雙下巴、下垂蘋果肌，呈現小V緊緻輪廓，所以很多陸客都很喜歡來台灣做此療程。</p> <p>極線音波拉皮也是另一熱門療程，美仕媞診所是採用因為美國原廠Ulthera，正宗音波拉皮，比一般韓國機種維持時間更長，可以有效拉提、緊緻輪廓，改善下垂鬆弛的肌膚，並同時改善肌膚膚質。</p> <div class=\"img-block\"><div class=\"img-block-item\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/about/5.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/about/5.jpg")))+" alt=\"美麗旅程\"></picture></div> <div class=\"img-block-item\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/about/6.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/about/6.jpg")))+" alt=\"美麗旅程\"></picture></div> <div class=\"img-block-item\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/about/7.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/about/7.jpg")))+" alt=\"美麗旅程\"></picture></div></div></div>")],2)}
